import React from "react"
import { Link } from "gatsby"
import LazyLoad from "react-lazyload"

import "./BlogItem.style.scss"

export default ({ post }) => (
  <div className="blog-item-wrapper">
    <div className="blog-item-wrapper__thumbnail">
      <a href={post.path}>
        <LazyLoad>
          <img
            alt="blog_img"
            src={
              post["featured_media"]
                ? post["featured_media"]["source_url"].replace(
                "http://localhost:3000",
                "https://vnrebates.net",
                )
                : ""
            }
          />
        </LazyLoad>
      </a>
    </div>
    <div className="blog-item-wrapper__body">
      <h4 className="title">
        <Link to={post.path} dangerouslySetInnerHTML={{ __html: post.title }}/>
      </h4>
      <span className="date">{post.date}</span>
      <p
        className="excerpt"
        dangerouslySetInnerHTML={{ __html: post.excerpt }}
      />
    </div>
  </div>
)
