import React from "react"
import { StaticQuery, graphql, Link, useStaticQuery } from "gatsby"
import { Card, ListGroup } from "react-bootstrap"
import styled from "styled-components"
import CategoryItem from "./CategoryItem"

const MostViewestItem = styled(ListGroup.Item)`
  font-size: 14px !important;
  padding: 0px !important;
  padding-bottom: 5px !important;
  border: none !important;
  display: flex;
  align-items: center;
  &:before {
    content: "»";
    padding-right: 5px;
    color: "#656565";
  }
  a {
    color: #337ab7;
  }
`

const RelatedNews = ({ posts, ...others }) => {
  const renderItem = ({ node }) => (
    <MostViewestItem key={`most_viewest_` + node.wordpress_id}>
      <Link to={node.path} dangerouslySetInnerHTML={{ __html: node.title }} />
    </MostViewestItem>
  )
  return (
    <Card {...others}>
      <Card.Title
        style={{
          marginBottom: 0,
          fontWeight: "bold",
          padding: 8,
          fontSize: 15,
        }}
      >
        BÀI VIẾT LIÊN QUAN
      </Card.Title>
      <Card.Body style={{ padding: 5, paddingLeft: 10 }}>
        <ListGroup variant="flush">{posts.map(renderItem)}</ListGroup>
      </Card.Body>
    </Card>
  )
}

export default RelatedNews
