import React from "react"
import { Link } from "gatsby"
import "./Category.style.scss"

export default ({ post }) => (
  <div className="post">
    <div className="post__logo">
      <img
        src={
          post["featured_media"]
            ? post["featured_media"]["source_url"].replace(
                "http://localhost:3000",
                "https://vnrebates.net"
              )
            : ""
        }
      />
    </div>
    <div className="post__body">
      <div className="post__body__title">
        <Link to={post.path} dangerouslySetInnerHTML={{ __html: post.title }} />
      </div>
      <div className="post__body__date">{post.date}</div>
      <div className="post__body__des">
        <p dangerouslySetInnerHTML={{ __html: post.excerpt }} />
      </div>
    </div>
  </div>
)
